                                    background: $cyan;
                                    height: 1px;
                                    width: 100%;
                                    clip-path: inset(0 100% 0 0);
                                    transition: .5s cubic-bezier(.25, 0, 0, 1);
                                }

                                &:hover::after {
                                    clip-path: inset(0 0 0 0);
                                }
