    ////SOCIALS MEDIA///
    .sm {
        width: 42px;
        height: 42px;
        position: absolute;
        right: 18px;
        color: $cyan;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 10;
        font-family: 'littleTitle';
        letter-spacing: 2px;
        font-size: 15px;
        overflow: hidden;
        font-weight: 600;

        @media screen and (max-width: 600px) {
            right: .8%;
            font-size: 3.3vw;
            width: 30px;
            height: 30px;
        }

        &.neonText {
            text-shadow: 0px;
            animation: blink 4s infinite alternate;
        }

        @keyframes blink {
            30% {
                opacity: 1;
                text-shadow: 0 0 0 $cyan, 0 0 2px $cyan, 0 0 2px $cyan;
            }

            32% {
                opacity: .25;
            }

            33% {
                opacity: 1;
            }

            35% {
                opacity: .25;
            }

            40% {
                opacity: 1;
            }
        }

        @keyframes blinkBtn {
            30% {
                opacity: 1;
                text-shadow: 0 0 0 $bleu, 0 0 1px $bleu, 0 0 1px $bleu;
            }

            32% {
                opacity: .65;
            }

            33% {
                opacity: 1;
            }

            35% {
                opacity: .65;
            }

            40% {
                opacity: 1;
                text-shadow: 0 0 0 $bleu, 0 0 1px $bleu, 0 0 1px $bleu;
            }
        }

        @keyframes inSM {
            to {
                transform: translateX(0);
            }
        }

        @keyframes outSM {
            to {
                transform: translateX(165%);
            }
        }

        @keyframes outImpairSM {
            to {
                transform: translateX(-165%);
            }
        }

        &__1 {
            bottom: 1.8%;

            @media screen and (max-width: 600px) {
                bottom: .5%;
            }

            &.mouseover::after {
                content: "FB";
                position: absolute;
                animation: outSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
            }

            &.mouseover::before {
                content: "FB";
                position: absolute;
                transform: translateX(-165%);
                animation: inSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
            }

            &.mouseout::after {
                content: "FB";
                position: absolute;
                animation: inSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
                transform: translateX(-165%);
            }

            &.mouseout::before {
                content: "FB";
                position: absolute;
                animation: outSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
                transform: translateX(0);
            }
        }

        &__2 {
            bottom: 6.2%;

            @media screen and (max-width: 600px) {
                bottom: 5.5%;
            }

            &.mouseover::after {
                content: "IG";
                position: absolute;
                animation: outImpairSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
            }

            &.mouseover::before {
                content: "IG";
                position: absolute;
                transform: translateX(165%);
                animation: inSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
            }

            &.mouseout::after {
                content: "IG";
                position: absolute;
                animation: inSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
                transform: translateX(165%);
            }

            &.mouseout::before {
                content: "IG";
                position: absolute;
                animation: outImpairSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
                transform: translateX(0);
            }
        }

        &__3 {
            bottom: 10.6%;

            @media screen and (max-width: 600px) {
                bottom: 10.5%;
            }

            &.mouseover::after {
                content: "IN";
                position: absolute;
                animation: outSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
            }

            &.mouseover::before {
                content: "IN";
                position: absolute;
                transform: translateX(-165%);
                animation: inSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
            }

            &.mouseout::after {
                content: "IN";
                position: absolute;
                animation: inSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
                transform: translateX(-165%);
            }

            &.mouseout::before {
                content: "IN";
                position: absolute;
                animation: outSM .5s cubic-bezier(.75, 0, 0, 1) forwards;
                transform: translateX(0);
            }
        }
    }

    ////TIMELINE///
    .containerTimeline {
        width: fit-content;
        height: fit-content;
        position: absolute;
        top: 27.7vh;
        z-index: 10;
        display: flex;
        right: 0;
        flex-direction: column;
        align-items: flex-end;
        padding: 0 18px 0 25px;
        overflow: hidden;
        transition: .75s cubic-bezier(.75, 0, 0, 1);
        clip-path: inset(0 0 0 70%);
        text-align: center;

        .timelineIndication {
            .indication {
                color: $cyan;
                font-family: "littleTitle";
                writing-mode: vertical-rl;
                text-orientation: mixed;
                letter-spacing: 8px;
                font-size: 15px;
                font-weight: 600;
                position: absolute;
                transform: translate(-50%, -50%);
                right: 22px;
                top: 50%;
                opacity: 1;
                transition: .5s cubic-bezier(.75, 0, 0, 1);
                filter: blur(0);

                &.switch {
                    opacity: 0;
                    letter-spacing: 25px;
                    filter: blur(2px)
                }
            }
        }

        .workShopButton {
            width: fit-content;
            margin: .6vh 0 .6vh 0;
            padding: 0 10px 0 10px;
            font-family: "littleTitle";
            overflow: hidden;
            color: $cyan;
            font-size: 15px;
            letter-spacing: .5px;
            position: relative;
            transition: .5s cubic-bezier(.75, 0, 0, 1);
            clip-path: inset(0 0 0 99%);
            font-weight: normal;

            &:hover {
                color: $bleu;
                font-weight: bold;
            }

            &::before {
                content: " ";
                width: 2px;
                right: 0;
                position: absolute;
                background: $cyan;
                transition: .5s cubic-bezier(.75, 0, 0, 1);
                z-index: -1;
            }

            &:hover::before {
                width: 100%;
                background: $cyan;
                right: 0;
            }

            &__1,
            &__2,
            &__3,
            &__4,
            &__5,
            &__6,
            &__7,
            &__8,
            &__9,
            &__10,
            &__11,
            &__12,
            &__13,
            &__14 {
                &::after {
                    content: " ";
                    position: absolute;
                    left: 0;
                    padding: 0 20px 0 20px;
                    transition: .5s cubic-bezier(.75, 0, 0, 1);
                    width: 100%;
                    height: 100%;
                    background: $cyan;
                    clip-path: inset(0 100% 0 0);
                    z-index: -1;
                }

                &:hover::after {
                    clip-path: inset(0 0 0 0);
                }
            }

            .mask {
                position: absolute;
                width: 100%;
                right: 0;
                background: $cyan;
            }
        }
    }

